  src: url("../fonts/Onest-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Onest";
  src: url("../fonts/Onest-SemiBold.ttf") format("truetype");
  font-style: normal;
