*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Onest", sans-serif;
  background: #fbfbfb;
  color: #000000;
  font-weight: 400;
  font-size: 17px;
  line-height: 1.3;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

a {
  color: #ffffff;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

[hidden] {
  display: none !important;
}

/* Mobile-menu */
[data-state=mobile-menu] .header__menu-wrapper {
  transform: translateX(0);
}
[data-state=mobile-menu] .overlay {
  opacity: 1;
  pointer-events: auto;
}

/* Swiper */
.swiper-nav {
  position: absolute;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  pointer-events: none;
}
@media only screen and (max-width: 991px) {
  .swiper-nav {
    justify-content: center;
    gap: 20px;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 37px;
  background-color: #000000;
  border-radius: 10px 6px 10px 6px;
  transform: skewX(-8deg);
  cursor: pointer;
  pointer-events: auto;
  transition: background-color 0.3s ease;
}
.swiper-nav .swiper-btn-prev:hover,
.swiper-nav .swiper-btn-next:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  transform: skewX(8deg);
}
.swiper-nav .swiper-button-disabled {
  filter: grayscale(100%);
  cursor: default;
}

/* Swiper pagination */
.swiper-pagination {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: unset !important;
  bottom: 16px !important;
  z-index: 12;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    display: flex;
  }
}
.swiper-pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  opacity: 0.5;
  background-color: #ffffff;
  margin: 0 !important;
}
.swiper-pagination .swiper-pagination-bullet-active {
  opacity: 1;
}

/* Blocks */
.container {
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media only screen and (max-width: 575px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  max-width: 300px;
  width: 100%;
  padding: 21px 20px;
  border-radius: 10px;
  box-sizing: border-box;
  background-color: #0264fa;
  color: #ffffff;
  font-size: 17px;
  line-height: 1.3;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  transition: 0.3s ease;
}
@media only screen and (max-width: 991px) {
  .button {
    font-size: calc(16px + (17 - 16) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .button {
    font-size: calc(14px + (16 - 14) * (100vw - 0px) / (768 - 0));
  }
}
.button:hover {
  background-color: rgba(2, 100, 250, 0.7);
}

.button-up {
  position: fixed;
  right: 50px;
  bottom: 35px;
  z-index: 12;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  padding: 5px;
  border-radius: 3px;
  background-color: rgba(2, 100, 250, 0.2);
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
  cursor: pointer;
}
@media only screen and (max-width: 991px) {
  .button-up {
    right: 15px;
    bottom: 15px;
  }
}
.button-up:hover {
  background-color: rgba(2, 100, 250, 0.3);
}
.button-up--visible {
  opacity: 1;
  pointer-events: auto;
}

.title {
  color: #000000;
  font-weight: 400;
  font-size: 40px;
  line-height: 1.2;
  text-align: center;
}
@media only screen and (max-width: 1279px) {
  .title {
    font-size: calc(35px + (40 - 35) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title {
    font-size: calc(30px + (35 - 30) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title {
    font-size: calc(22px + (30 - 22) * (100vw - 0px) / (768 - 0));
  }
}

.footer__adv {
  position: relative;
  cursor: pointer;
}
.footer__adv:hover .footer__adv-tooltip {
  opacity: 1;
  pointer-events: auto;
}
.footer__adv span {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #ffffff;
  font-size: 12px;
}
.footer__adv span::before {
  content: "";
  width: 5px;
  height: 5px;
  background-color: #ffffff;
}
.footer__adv-tooltip {
  position: absolute;
  left: -100px;
  bottom: 100%;
  width: 270px;
  border: 2px solid #0264fa;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(11px);
  border-radius: 2px;
  padding: 20px 10px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}
.footer__adv-tooltip-block {
  color: #171717;
  font-size: 12px;
  line-height: 1.2;
}
.footer__adv-tooltip-block a {
  color: #171717;
  text-decoration: underline;
}
.footer__adv-tooltip-block a:hover {
  text-decoration: none;
}
.footer__adv-tooltip-block + .footer__adv-tooltip-block {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid #171717;
}

.hero {
